body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

/* Universal Styles */

html {
  font-size: 18px;
  background-image: url(madonghui.github.io/static/media/mobile.56a27114.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-position: right bottom;
}

html, body { scroll-behavior:smooth; }

@media only screen and (max-width: 560px) {
  html {
    font-size: 15px;
  }
}

input:focus {
  outline: none;
}

/* Reusable Component - Image Container */

.image-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  overflow: hidden;
  background: #000;
}

.image-container img {
  width: 100%;
}

/* App Styles */

.App {
  font-family: 'Work Sans', sans-serif;
}

h1 {
  font-family: 'Titillium Web', sans-serif;
	position: fixed;
	color: white;
	font-size: xx-large;
	font-weight: 900;
	top: 50px;
	padding: 15px 50px 15px 20px;
	background-color: rgba(0,0,0,0.8);
}
.BusinessList {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
  }
  
.Business {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    width: 16.66rem;
    margin: 0 .5rem 2.3rem .5rem;
  }
  
  .Business .image-container {
    height: 16.66rem;
    margin-bottom: 1rem;
  }
  
  .Business h2 {
    margin-bottom: .5rem;
    font-size: 1.2rem;
    font-weight: 600;
  }
  
  .Business-information {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
  }
  
  .Business-information p {
    font-size: .88rem;
    font-weight: 300;
    line-height: 1rem;
  }
  
  .Business-address {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-between;
            justify-content: space-between;
  }
  
  .Business-reviews {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    text-align: right;
  }
  
  .Business-reviews h3 {
    color: #CCA353;
    font-weight: 600;
  }
  
  .Business-reviews .rating {
    font-size: .88rem;
    line-height: 1rem;
  }
.SearchBar {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    height: 80vh;
  
  }
  
  .SearchBar-sort-options ul {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    margin-bottom: 2.22rem;
    color: #2f4858;
  }
  
  .SearchBar-sort-options li {
    cursor: pointer;
    border-bottom: 1px solid #fff;
    padding: 0px 2rem .33rem 2rem;
    line-height: 1.13;
    text-align: center;
    font-weight: 300;
    font-size: 1rem;
    transition: color .25s;
  }
  
  .SearchBar-sort-options li:hover {
    color: #58a344;
  }
  
  .SearchBar-sort-options li.active,
  .SearchBar-sort-options li.active:hover {
    border-bottom: 1px solid black;
    color: black;
    font-weight: 600;
  }
  
  .SearchBar-fields {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    margin-bottom: 2.88rem;
  }
  
  .SearchBar-fields input {
    width: 50rem;
    padding: .66rem 5rem;
    font-family: 'Titillium Web', sans-serif;
    font-style: italic;
    background-color: rgba(0, 0, 0, 0);
    border-style: none;
  }

  ::-webkit-input-placeholder {
    opacity: 1;
  }

  ::-ms-input-placeholder {
    opacity: 1;
  }

  ::placeholder {
    opacity: 1;
  }
  
  .SearchBar-fields input:last-child {
    margin-right: 0;
  }


  
  .SearchBar-submit {
    text-align: center;
  }
  
  .SearchBar-submit a {
    border-radius: 2px;
    padding: .72rem 1.7rem;
    background-color: black;
    color: #ffffff;
    font-weight: 600;
    transition: background-color .5s;
  }
  
  .SearchBar-submit a:hover {
    cursor: pointer;
    background-color: #a7874b;
  }
  
  #foodName {
    font-size: 6rem;
    font-weight: 300;
  }

  #locationName {
    font-size: 3rem;
    font-weight: 100;
  }

  @media only screen and (max-width: 560px) {

  
    .SearchBar-sort-options ul {
      margin-left: 2rem;
      margin-right: 2rem;
    }
  
    .SearchBar-fields {
      -webkit-flex-direction: column;
              flex-direction: column;
      -webkit-align-items: center;
              align-items: center;
    }

    .SearchBar-fields input {
      width: 85%;
    }

    .SearchBar-submit {
      margin: 0 2rem;

    }

    #foodName {
      font-size: 4rem;
      font-weight: 300;
    }

    #locationName {
      font-size: 2rem;
      font-weight: 100;
    }
  }

